* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a {
  color: #333;
  text-decoration: none;
}

.App {
  margin: 5vh 0 0 0;
  padding: 0 10vw;
}

.add-btn {
  display: inline-inline-block;
  border: none;
  background-color: #555;
  color: #dcdcdc;
  padding: 7px 20px;
  cursor: pointer;
}

.add-btn:hover {
  background-color: #666;
}
